<template>
  <div id="Remarks" class="remarks">
    <div class="Remarks-main">
      <form @submit.prevent="submit">
        <table class="table">
          <tr class="field">
            <td class="tdle">
                <img src="../images/icon/azr.png">
                姓名
            </td>
            <td class="tdwid">
              <input type="text" v-model="name">
            </td>
          </tr>
          <tr class="field fieldTop">
            <td class="tdle">
              <img src="../images/icon/dh.png">
              电话
            </td>
            <td class="tdwid">
              <input type="text" v-model="Telephone" @change="Newhone">
            </td>
          </tr>
          <tr class="field fieldTop">
            <td class="tdle">
              <img src="../images/icon/azsj.png">
              时间
            </td>
            <td class="tdwid">
              <div class="">
                <div>
                  <input type="text" class="time" @click="openByDrop($event)" v-model="calendar3.display" readonly>
                </div>
                <transition name="fade">
                  <div class="calendar-dropdown" :style="{'left':calendar3.left+'rem','top':calendar3.top+'rem'}" v-if="calendar3.show">
                    <calendar :zero="calendar3.zero" :lunar="calendar3.lunar" :value="calendar3.value" :begin="calendar3.begin" :end="calendar3.end" @select="calendar3.select"></calendar>
                  </div>
                </transition>
              </div>
            </td>
          </tr>
          <tr class="field fieldTop remar field-img">
            <td class="tdle">
              <img src="../images/icon/bz.png">
              备注
            </td>
            <td class="tdwid">
            <textarea  v-model="remarks"></textarea>
            </td>
          </tr>
          <tr class="field field-img fieldTop">
            <td class="tdle">
              <img src="../images/icon/fj.png">
              附件
            </td>
            <td class="tdwid imglist">
              <span @click="Photograph()"><img class="addimg" src="../images/icon/tjtp.png"></span>
            </td>
          </tr>
        </table>
      </form>
    </div>
    <input type="submit" class="submit" value="提交" @click="godevilist()">
    <!--<v-scan @click.native="scanClick()"></v-scan>-->
    <!--<promptBox  v-show="probox.show"></promptBox>-->
  </div>
</template>

<script>
//  import promptBox from './promptBox.vue'
  import calendar from './calendar.vue'
  //  import Scan from './scan.vue';
  export default {
    name: 'Remarks',
    data(){
      console.log("---------",this.$route.params); //上一页带过来的参数
      return {
        name: "",
        time:"",
        Telephone: "",
        remarks: "",
        probox:{
          show:false
        },
        calendar3:{
          display:"",
          show:false,
          zero:true,
          value:[], //默认日期
          lunar:true, //显示农历
          select:(value)=>{
            this.calendar3.show=false;
            this.calendar3.value=value;
            this.calendar3.display=value.join("-");
          }
        }
      }

    },
    created (){
      this.$http.post('/static/findTodoTasks.json',{
            productType:"1",
            pageNum:1,
            pageLimit:8
        },{
          emulateJSON:true
        })
        .then(
          function(response){
            if(response.data.success === 0) {
              this.items = response.data.data.result;
            }
          },
          function(response){
            console.log(response);
          }
        );

    },
    methods: {
      Newhone: function() {
        var reg = {
          email: /^(\w-*\.*)+@(\w-?)+(\.\w{2,})+$/,
          phone: /^1(3|4|5|7|8)\d{9}$/
        }
        if(reg.phone.test(this.Telephone)){

        }else {
//          this.probox.show = true;
//          onAddnew();
        }
      },
      openByDrop(e){
        this.calendar3.show=true;
        this.calendar3.left=0.5;
        this.calendar3.top=2.7;

        e.stopPropagation();
        window.setTimeout(function(){
          document.addEventListener("click",function(e){
            this.calendar3.show=false;
            document.removeEventListener("click",function(){},false);
          },false);
        },1000)
      },
      godevilist:function(e){   //提交数据
        var str = {},remark,sendJson=[],
          names,phones,times,contents,relatedAttachIds,
          relatedAttachNames,arrt;
        names = this.name;             //姓名
        phones = this.Telephone;       //电话
        times = $(".time").val();      //时间
        contents = this.remarks;       //备注
        relatedAttachIds = "";         //附件id
        relatedAttachNames = ""        //附件名称
        str.name= names,
        str.phone= phones,
        str.time= times,
        str.content= contents,
        str.relatedAttachId= relatedAttachIds,
        str.relatedAttachName= relatedAttachNames;
        remark = JSON.stringify(str);
        this.$http.post('/test/weixin/updateRegister'+window.times,{
            assetCode:window.sessionStorage.devlicode,
            assetTrackInfos:remark
          },{
            emulateJSON:true
          })
          .then(
            function(response){
              if(response.data.success === 0) {
                this.$router.push({
                  name: 'DeviceList',
                  params:{
                    id:this.$route.params.site,
                    assetStatus:'ZCZT-SY'
                  }
                })
              }
            },
            function(response){
              console.log(response);
            }
          )
      },
      Photograph:function(){  //拍照功能
        this.$http.get('/static/config.json',{
            params: {
              id:this.$route.params.id
            }
          })
          .then(
            function(response){
              if(response.data.success === 0) {
                console.log("responser", response.data.data.appId);
                wx.config({
                  debug: true, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
                  appId: response.data.data.appId, // 必填，公众号的唯一标识
                  timestamp: response.data.data.timestamp, // 必填，生成签名的时间戳
                  nonceStr: response.data.data.nonceStr, // 必填，生成签名的随机串
                  signature: response.data.data.signature, // 必填，签名
                  jsApiList: [
                    'chooseImage',//拍照或从手机相册中选图接口
                    'scanQRCode'// 微信扫一扫接口
                  ] // 必填，需要使用的JS接口列表
                });
                wx.ready(function () {
                  wx.scanQRCode({
                    needResult: 0, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
                    scanType: ["qrCode", "barCode"], // 可以指定扫二维码还是一维码，默认二者都有
                    success: function (res) {
                      console.log("res", res);
                      var result = res.resultStr; // 当needResult 为 1 时，扫码返回的结果
                    }
                  });
                });
              }
            },
            function(response){
              console.log(response);
            }
          )
      },
      /*scanClick: function(){ //扫一扫功能
        console.log("<<<=======>>>");
        wx.ready(function() {
          wx.scanQRCode({
            needResult: 0, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
            scanType: ["qrCode", "barCode"], // 可以指定扫二维码还是一维码，默认二者都有
            success: function (res) {
              console.log("res",res);
              var result = res.resultStr; // 当needResult 为 1 时，扫码返回的结果
            }
          });
        });
      },*/
      setTitle : function(t) { //更新title  有问题，需要刷新才能更换
        console.log(t);
        document.title = t;
        var i = document.createElement('iframe');
        i.src = '//m.baidu.com/favicon.ico';
        i.style.display = 'none';
        i.onload = function() {
          setTimeout(function(){
            i.remove();
          }, 9)
        }
        document.body.appendChild(i);
      }
    },
    mounted(){
      this.setTitle('备注')
    },
    components:{
      calendar
//      promptBox
//      'v-scan': Scan
    //      calendar: require('./calendar.vue')
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  @import "../css/remarks.css";
</style>



