<template>
  <div id="rosterSelectSite" class="rosterSelectSite">
    <div :class="'list'">
      <ul>
        <li v-for="item in items"   @click="returnHome(item.id,item.worksiteName)" v-bind:id="item.id">
          <a>
            <p>{{item.worksiteName}}</p>
          </a>
        </li>
      </ul>
    </div>
    <!--<v-scan @click.native="scanClick()"></v-scan>-->
  </div>
</template>

<script>
//  import Scan from './scan.vue';
  export default {
    name: 'RosterSelectSite',
    data(){
      console.log("---------",this.$route);
      return {
        title:"请选择工地",
        items:[]
      }
    },
    created (){
      this.$http.post('/test/weixin/findWorkSite'+window.times,{
          params: {
          }
        },{
          emulateJSON:true
        })
        .then(
          function(response){
            console.log(response);
            this.items =response.data.data;
            //this.$set(this.items,response.data.data);
            console.log(this.items);
          },
          function(response){
            console.log(response);
          }
        )
    },
    methods: {
      returnHome: function(e,i) {
        var workchua = e +','+i;
        this.$http.post('/test/weixin/saveHistory'+window.times,{
            userName:window.username,
            userExtendInfo:workchua,
          },{
            emulateJSON:true
          })
          .then(
            function(response){
              console.log(response);
            },
            function(response){
              console.log(response);
            }
          )
        this.$router.push({
          name: 'Home',
          params:{id:e,workSite:i}
        })
      },
      setTitle : function(t) {
        console.log(t);
        document.title = t;
        var i = document.createElement('iframe');
        i.src = '//m.baidu.com/favicon.ico';
        i.style.display = 'none';
        i.onload = function() {
          setTimeout(function(){
            i.remove();
          }, 9)
        }
        document.body.appendChild(i);
      }
    },
    mounted(){
      this.setTitle('选择工地')
    },
    components:{
//      'v-scan': Scan
//      calendar: require('./calendar.vue')
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  @import "../css/rosterSelectSite.css";
</style>



