<template>
  <div id="UnregisteredDevice" class="unregisteredDevice">
    <div :class="'list'">
      <div class="hard"></div>
      <v-scroll :on-refresh="onRefresh" :on-infinite="onInfinite">
        <ul :style="{'min-height':fullHeight+'px','background-color':'#ffffff'}">
          <li class="unregisteredDevice-li" v-for="item in listdata"  v-bind:assetCode="item.assetCode">
            <a @click="goRoute(item.assetCode)">
              <span class="unregisteredDevice-li-le">
                <img v-bind:src="item.icon" />
              </span>
              <span class="unregisteredDevice-li-re">
                <p>设备名称：<i>{{item.assetName}}</i></p>
                <p>设备编号：<i>{{item.deviceCode}}</i></p>
              </span>
            </a>
          </li>
          <li class="unregisteredDevice-li" v-for="item in downdata"  v-bind:assetCode="item.assetCode">
            <a @click="goRoute(item.assetCode)">
              <span class="unregisteredDevice-li-le">
                <img v-bind:src="item.icon" />
              </span>
              <span class="unregisteredDevice-li-re">
                <p>设备名称：<i>{{item.assetName}}</i></p>
                <p>设备编号：<i>{{item.deviceCode}}</i></p>
              </span>
            </a>
          </li>
          <noequi class="noequip"></noequi>
        </ul>
      </v-scroll>
    </div>
    <!--<v-scan @click.native="scanClick()"></v-scan>-->
  </div>
</template>

<script>
//  import Scan from './scan.vue';
  import noequi from './NoEquipment.vue'
  import Scroll from './scroll.vue';
  export default {
    name: 'UnregisteredDevice',
    data(){
      return {
        counter : 1, //默认已经显示出10条数据 count等于一是让从11条开始加载
        num : 10,  // 一次显示多少条
        pageStart : 0, // 开始页数
        pageEnd : 0, // 结束页数
        listdata: [], // 下拉更新数据存放数组
        downdata: [],  // 上拉更多的数据存放数组
        fullHeight:""
      }
    },
    /*created (){
      this.$http.get('/static/deviceList.json',{
          params: {
            productType:"1",
            pageNum:1,
            pageLimit:8
          }
        })
        .then(
          function(response){
            this.items =response.data.data.result;
            //this.$set(this.items,response.data.data.result);
            console.log(this.items);
          },
          function(response){
            console.log(response);
          }
        )
    },
    methods: {
      goRoute: function(e) {
        this.$router.push({
          name: 'Register',
          params:{id:e}
        })
      },
      setTitle : function(t) {
        console.log(t);
        document.title = t;
        var i = document.createElement('iframe');
        i.src = '//m.baidu.com/favicon.ico';
        i.style.display = 'none';
        i.onload = function() {
          setTimeout(function(){
            i.remove();
          }, 9)
        }
        document.body.appendChild(i);
      }
    },
    mounted(){
      this.setTitle('未登记设备')
    }*/
    mounted : function(){
      this.fullHeight=document.documentElement.clientHeight;
      this.getList();
      this.setTitle('未登记设备')
    },
    methods: {
      getList(){
        var sStorage=window.sessionStorage,
          pasid = this.$route.params.id || "",
          pasassetStatus = this.$route.params.assetStatus || "",
          sstorassetCode = sStorage.assetCode || "",
          sstorworkSiteId = sStorage.workSiteId || "",
          deviceid,devicecode;
        if(pasid == undefined || pasid == "" || pasid ==null){
          deviceid = sstorworkSiteId;
        }else{
          deviceid=pasid
        }
        let vm = this;
        vm.$http.post('/test/weixin/findByStatus'+window.times,{
            site:deviceid,
            assetStatus:'ZCZT-WSY'
        },{
          emulateJSON:true
        }).then(function(response){
          if(response.data.success === 0) {
            if(response.data.data !=""){
              $('.noequip').css('display', 'none');
              vm.listdata = (response.data.data).slice(0, 10);
            }else{
              $('.noequip').css('display','block');
            }
          }
        }, function(response){
          console.log('error');
        });
      },
      onRefresh(done) {
        this.getList();
        done() // call done
      },
      onInfinite(done) {
        var sStorage=window.sessionStorage,
          pasid = this.$route.params.id || "",
          pasassetStatus = this.$route.params.assetStatus || "",
          sstorassetCode = sStorage.assetCode || "",
          sstorworkSiteId = sStorage.workSiteId || "",
          deviceid,devicecode;
        if(pasid == undefined || pasid == "" || pasid ==null){
          deviceid = sstorworkSiteId;
        }else{
          deviceid=pasid
        }
        let vm = this;
        vm.$http.post('/test/weixin/findByStatus'+window.times,{
            site:deviceid,
            assetStatus:'ZCZT-WSY'
        },{
          emulateJSON:true
        }).then(function(response){
          if(response.data.success === 0) {
            vm.counter++;
            vm.pageEnd = vm.num * vm.counter;
            vm.pageStart = vm.pageEnd - vm.num;
            let arr = response.data.data;
            let i = vm.pageStart;
            let end = vm.pageEnd;
            for (; i < end; i++) {
              let obj = {};
              obj["id"] = arr[i].id;
              obj["icon"] = arr[i].icon;
              obj["assetName"] = arr[i].assetName;
              obj["assetCode"] = arr[i].assetCode;
              vm.downdata.push(obj);
              if ((i + 1) >= response.data.data.result.length) {
                this.$el.querySelector('.load-more').style.display = 'none';
                return;
              }
            }
            done() // call done
          }
        }, function(response){
          console.log('error');
        });
      },
      setTitle : function(t) {
        document.title = t;
        var i = document.createElement('iframe');
        i.src = '//m.baidu.com/favicon.ico';
        i.style.display = 'none';
        i.onload = function() {
          setTimeout(function(){
            i.remove();
          }, 9)
        }
        document.body.appendChild(i);
      },
      goRoute: function(e) {
        var sStorage=window.sessionStorage;
        sStorage.unrgcode = e;
        this.$router.push({
          name: 'Register',
          params:{
            assetCode:e,
            workSite:this.$route.params.workSite,
            workSiteId:this.$route.params.workSiteId,
            site:this.$route.params.id,
            assetStatus:'ZCZT-WSY'
          }
        })
      }
    },
    components : {
      'noequi':noequi,
//      'v-scan': Scan,
      'v-scroll': Scroll
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  @import "../css/UnregisteredDevice.css";
</style>



